import React, { useEffect, useState } from 'react'
import './style.css'
import QrReader from 'react-qr-reader'
import McInput from 'components/Common/McInput';
import { useHistory, useParams } from 'react-router-dom';

import { sendGamedata } from 'components/Common/core';

import { useActiveScreen } from '../../../../store/ActiveScreen';
import { useAuth } from '../../../../store/authStore'
import { userActiveLevelDetailsStore } from '../../../../store/ActiveLevelDetailsStore'
import { levelstore } from '../../../../store/levelStore';
import { useColourObjectStore } from '../../../../store/ColourStore';
import { useActivePopup } from '../../../../store/ActivePopup';

import * as close from '../../../../assets/close_icon.svg';
import { instancestore } from '../../../../store/InstanceStore';

const QRscanner = (props: any) => {

    const { colourObj } = useColourObjectStore();

    const { instance, activeInstanceData, loadactiveInstancebyID } = instancestore();

    const { setActivePopup } = useActivePopup();
    const { setActiveScreen } = useActiveScreen();

    const { auth } = useAuth();

    const history = useHistory();

    const { level, loadlevelwithid } = levelstore();

    const { loadActiveleveldetailsbyqr } = userActiveLevelDetailsStore();

    const [instancedata, setinstancedata] = useState('');

    useEffect(() => {

        if (activeInstanceData) {
            loadlevelwithid(activeInstanceData.id)
        }
        
    }, [])

    const findid = (id: any) => {
        let a: number = null;
        (level) && level.forEach((element: any) => {
            if (String(element.l_qr_id) === String(id)) {
                a = Number(element.id)
            }
        });
        return a;
    }

    const Instancefilter = () => {
        if (!activeInstanceData) {
            return (
                <div className="QRInstancefilter">
                    <McInput
                        type="picker"
                        id="input_instancedatatextbox"
                        name={`Pick a Game`}
                        required={true}
                        value={instancedata}
                        onchange={(data: any) => {
                            let formattedgamename = data.value.replace(/\s/g, "").toLowerCase()
                            setinstancedata(formattedgamename)
                            loadactiveInstancebyID(data.key)
                            history.push(`/${String(formattedgamename)}/`)
                            setActivePopup("")
                        }}
                        options={(instance) && instance.map(
                            (obj: any) => {
                                return { "key": obj.id, "value": obj.i_title }
                            }
                        )}
                    />
                    <div className='QRscannerText2' style={{ color: colourObj.color_m2 }}>Or</div>
                </div>
            )
        } else return null
    }

    return (
        <div className='taskslist_scannerpopup_bg'>
            <div className='taskslist_scannerpopup_container'>

                <Instancefilter />

                <div className='QRscannerText' style={{ color: colourObj.color_m2 }}>Scan our QR CODE to unlock the level!</div>

                <div className='taskslist_scannerpopup' style={{ borderColor: colourObj.color_m2 }}>
                    {(activeInstanceData) &&
                        <div className='close_button' onClick={() => { setActivePopup("") }}>
                            <img className='qr_close_button' src={close} />
                        </div>
                    }
                    <QrReader
                        onScan={(data: any) => {
                            if (data !== null) {
                                console.log(data);
                                let a = data.split("/")
                                let b = a[a.length - 2]
                                let id = String(b).substring(4, b.length)
                                let iid = a[a.length - 3]
                                console.log("The level id is ", id, "the instace id is", iid);
                                if (auth) {
                                    if (activeInstanceData) {
                                        if (String(activeInstanceData.id) === String(iid)) {
                                            loadActiveleveldetailsbyqr(id)
                                            setActiveScreen("Level")
                                            sendGamedata(findid(id), "LEVEL", "", "", "ACTIVE", auth)
                                            setActivePopup("")
                                        }
                                        else
                                            if (String(activeInstanceData.id) === "default") {
                                                window.location.href = data
                                                window.location.reload()
                                            }
                                    }
                                }
                            }
                        }
                        }
                        facingMode={"environment"}
                        onError={(data: any) => { console.log(data) }}
                    />
                </div>
            </div>
        </div>
    )
}

export default QRscanner
