import React, { useEffect, useState } from 'react'
import './style.css'

import * as google from '../../../../assets/Clean Walk/google.svg'
import * as facebook from '../../../../assets/Clean Walk/facebook.svg'
import { GoogleLogin } from 'react-google-login';
import FacebookProvider, { Login } from 'react-facebook-sdk';

import { Sociallogin } from '../../../../utils/actions'
import { useAuth } from '../../../../store/authStore';
import { userstore } from '../../../../store/userStore';
import { useActiveScreen } from '../../../../store/ActiveScreen';

import { instancestore } from '../../../../store/InstanceStore';
import { useActivePopup } from '../../../../store/ActivePopup';
import { useLoading } from '../../../../store/Loading';
import Cookies from 'components/Common/Cookies';


const LoginPopup = (props: any) => {

  const { setActiveScreen } = useActiveScreen();
  const { setActivePopup } = useActivePopup();

  const { setAuth } = useAuth()
  const { setselfuser } = userstore()
  const { instance, loadinstance, loadactiveInstancebyID } = instancestore();
  const { pushloading, poploading, isloading } = useLoading();

  const [cookie_popup, setcookie_popup] = useState(navigator.cookieEnabled)

  const loginCallback = async (data: any, errorresponse: any) => {
    if (data.status === 200) {
      console.log('response ' + JSON.stringify(data));
      localStorage.setItem('AuthToken', JSON.stringify(data.data.results.token));
      localStorage.setItem('UserDetails', JSON.stringify(data.data.results.user_details));
      localStorage.setItem('UsedSocialLoginMethod', "true");
      setAuth(data.data.results.token)
      setselfuser(data.data.results.user_details)
    } else {
      console.log('error ' + JSON.stringify(data));
      console.log('error ' + JSON.stringify(errorresponse));
    };
  }

  return (
    <div className='popupformcontainer'>
      <div className="login_button_container">
        <GoogleLogin
          clientId="581422038025-rte3a06d7kumasu887n64uikerfigmiv.apps.googleusercontent.com"
          render={renderProps => (
            <button className="googlebutton shadow" onClick={renderProps.onClick} disabled={renderProps.disabled}>
              <img src={google} className="login_googleicon" />
              <div className="buttontext">Continue with Google</div>
            </button>
          )}
          buttonText="Login"
          onSuccess={Response => {
            console.log(Response)
            var formData = new FormData()
            let userInfo = Response.profileObj
            formData.append('lastname', userInfo.familyName);
            formData.append('firstname', userInfo.givenName);
            formData.append('photo_url', userInfo.imageUrl);
            formData.append('auth_provider', "google");
            formData.append('email', userInfo.email);
            formData.append('username', userInfo.googleId);
            localStorage.setItem("Bearer", JSON.stringify(Response.accessToken))
            Sociallogin(loginCallback, formData)
            setActiveScreen("Home")
            setActivePopup("")
          }
          }
          onFailure={(Response: any) => { console.log(Response) }}
          cookiePolicy={'single_host_origin'}
        />
      </div>
      <div className="login_button_container">
        <FacebookProvider appId="494516388401593">
          <Login
            scope="email"
            onResponse={(response: any) => {
              console.log(response.profile)
              console.log(Response)
              var formData = new FormData()
              let userInfo = response.profile
              formData.append('lastname', userInfo.first_name);
              formData.append('firstname', userInfo.last_name);
              formData.append('photo_url', "");
              formData.append('auth_provider', "fb");
              formData.append('email', userInfo.email);
              formData.append('username', userInfo.id);
              Sociallogin(loginCallback, formData)
            }}
            onError={(response: any) => {
              console.log(response)
            }}
          >
            <button className="facebookbutton shadow">
              <img src={facebook} className="login_facebookicon" />
              <div className="buttontext">Continue with Facebook</div>
            </button>
          </Login>
        </FacebookProvider>
      </div>
      <Cookies />
    </div>
  )
}

export default LoginPopup